<template>
    <div>
        <input type="text" v-model="text" placeholder = "Add your post...">
        <input type="submit" value="Add Post" v-on:click = "addPost">
        
        <ul>
            <li v-bind:key = "post._id" v-for = "post in posts">
                {{post.text}}
            <button v-on:click = "deletePost(post._id)">remove</button>
            </li>
        </ul>
    </div>
</template>

<script>
import axios from 'axios';
import PostsServive from '../PostsService.js'

export default {
    name : "PostsComponent",
    data() {
        return {
            posts : [],
            text : ''
        }
    },
    methods: {
        async addPost(){
            await PostsServive.addPost(this.text);
            this.posts = await PostsServive.getPosts();
        },
        async deletePost(id){
            await PostsServive.deletePost(id);
            this.posts = await PostsServive.getPosts();
        }
        
    },
    async created(){
        // await axios.get('http://localhost:4000/api/posts')
        // .then(res => this.posts = res.data )
        // .catch(err => console.log(err));
        try{
            this.posts = await PostsServive.getPosts();
            
        }catch{
            
        }
        

    }
}
</script>

