import axios from 'axios';
const url = 'api/posts/';

class PostsService {

    static async getPosts() {
        const output = await axios.get(url);
        return output.data ;
        
    }

    static async addPost(text){
        await axios.post(url,{
          text,
          createdAt:"3/4/3333"
        });

    }

    static async deletePost(id){
        await axios.delete(url+`${id}`);
    }
}

export default PostsService